/* global cast */
import React, { useEffect, useState, useRef } from "react";
import { TVPlayer } from "react-tv-player";

function App() {
  const [videoUrl, setVideoUrl] = useState(null);
  const playerRef = useRef(null);

  useEffect(() => {
    function initCastReceiver() {
      if (!window.cast || !window.cast.framework) {
        console.error("Google Cast SDK не загружен.");
        return;
      }

      const context = cast.framework.CastReceiverContext.getInstance();
      const playerManager = context.getPlayerManager();

      playerManager.setMessageInterceptor(
        cast.framework.messages.MessageType.LOAD,
        (loadRequestData) => {
          console.log("🔹 Видео получено:", loadRequestData.media.contentUrl);
          setVideoUrl(loadRequestData.media.contentUrl);
          return loadRequestData;
        },
      );

      context.addCustomMessageListener("urn:x-cast:paradigma", (event) => {
        const { action, value } = event.data;
        console.log("🔹 Получено сообщение:", action, value);

        if (!playerRef.current) return;

        switch (action) {
          case "play":
            playerRef.current.play();
            break;
          case "pause":
            playerRef.current.pause();
            break;
          case "seek":
            playerRef.current.seekTo(value, "seconds");
            break;
          case "setVolume":
            playerRef.current.setVolume(value);
            break;
          case "mute":
            playerRef.current.mute(value);
            break;
          default:
            console.warn("⚠️ Неизвестное действие:", action);
        }
      });

      context.start();
    }

    // Ждем полной загрузки страницы
    if (document.readyState === "complete") {
      initCastReceiver();
    } else {
      window.onload = initCastReceiver;
    }
  }, []);

  return (
    <div
      style={{
        background: "black",
        height: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
    >
      {videoUrl ? (
        <TVPlayer
          src={videoUrl}
          autoPlay
          controls
          onReady={(player) => (playerRef.current = player)}
        />
      ) : (
        <h1 style={{ color: "white" }}>Ожидание видео...</h1>
      )}
    </div>
  );
}

export default App;
